import React, { useState, useEffect } from 'react'
import { Title, Loading, Modal } from './Components'
import moment from 'moment'
import { getUsername, getToken, getRole } from './modules/storageHelper'
import { useParams } from 'react-router-dom'

const ep = 'https://jadwalsetda.herokuapp.com/api/events/'

function Events(props) {
  const { type } = useParams()
  const role = getRole()
  const username = getUsername()

  // data
  const [events, setEvents] = useState([])
  const [places, setPlaces] = useState([])

  // filters
  const [month, setMonth] = useState(moment().local().month() + 1)
  const [year, setYear] = useState(moment().local().year())
  const [searchVal, setSearchVal] = useState('')

  const [isLoading, setIsLoading] = useState(false)
  const [isModalVisible, setIsModalVisible] = useState(false)
  const [selectedEvent, setSelectedEvent] = useState(null)

  let title
  switch (type) {
    case 'setda':
      title = 'Gedung & Ruangan'
      break;
    case 'adipura':
      title = 'Sasana Adipura Kencana'
      break;
    case 'bupati':
      title = 'Bupati'
      break;
    case 'wakil':
      title = 'Wakil Bupati'
      break;
    case 'sekda':
      title = 'Sekretaris Daerah'
      break;
    default:
      break;
  }

  let printEp = 'https://jadwalsetda.herokuapp.com/print.html?'
  if (type) printEp += `type=${type}`
  let fromLc, toLc
  if (month && year) {
    fromLc = moment(`${year})-${month}`, 'YYYY-MM').startOf('month').local().format('YYYY-MM-DD HH:mm')
    toLc = moment(`${year})-${month}`, 'YYYY-MM').endOf('month').local().format('YYYY-MM-DD HH:mm')
    printEp += `&from=${fromLc}&to=${toLc}`
  }

  const range = (start, stop, step) => Array.from(
    { length: (stop - start) / step + 1 },
    (_, i) => start + (i * step)
  )

  function getEvents() {
    setEvents([])
    setIsLoading(true)

    let from, to
    let epx = ep + '?'
    if (type) epx += `type=${type}`
    if (month && year) {
      from = moment(`${year})-${month}`, 'YYYY-MM').startOf('month').utc().format('YYYY-MM-DD HH:mm')
      to = moment(from, 'YYYY-MM-DD HH:mm').add(1, 'months').endOf('month').utc().format('YYYY-MM-DD HH:mm')
      epx += `&from=${from}&to=${to}`
    }

    fetch(epx)
      .then(res => res.json())
      .then(docs => {
        setIsLoading(false)
        setEvents(docs)
      })
      .catch(err => {
        console.log(err)
        setIsLoading(false)
      })
  }

  function getPlaces() {
    fetch('https://jadwalsetda.herokuapp.com/api/places')
      .then(res => res.json())
      .then(docs => {
        setPlaces(docs)
      })
      .catch(err => {
        console.log(err)
      })
  }

  useEffect(() => {
    getEvents()
    getPlaces()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [type, month, year])

  function match(c) {
    return c
      .toLowerCase()
      .indexOf(searchVal.toLowerCase())
      > -1
  }

  return (
    <>
      <Title>
        Jadwal&nbsp;
        {/* {type.charAt(0).toUpperCase() + type.slice(1)} */}
        {title ? title : null}
      </Title>

      <div className="box">

        <div className="level">
          <div className="level-left">

            <div className="level-item">
              <div className="field has-addons">
                <div className="control">
                  <div className="select">
                    <select value={month}
                      onChange={e => setMonth(parseInt(e.target.value))}
                    >
                      {range(1, 12, 1).map(m =>
                        <option key={m} value={m}>{moment(m, 'M').format('MMMM')}</option>
                      )}
                    </select>
                  </div>
                </div>
                <div className="control">
                  <div className="select">
                    <select value={year}
                      onChange={e => setYear(parseInt(e.target.value))}
                    >
                      {range(2018, moment().local().add(5, 'years').year(), 1).map(y =>
                        <option key={y} value={y}>{y}</option>
                      )}
                    </select>
                  </div>
                </div>
              </div>
            </div>

            <div className="level-item">
              <div className="field has-addons">
                <div className="control">
                  <input
                    type="text"
                    className="input"
                    placeholder="ketik untuk mencari..."
                    value={searchVal}
                    onChange={e => setSearchVal(e.target.value)}
                  />
                </div>
                <div className="control">
                  <button
                    type="button"
                    className={`button${
                      searchVal ? ' is-danger' : ''
                      }`}
                    onClick={() => setSearchVal('')}
                  >
                    <span className="icon">
                      <i className="fas fa-backspace" />
                    </span>
                  </button>
                </div>
              </div>
            </div>

          </div> {/* level left ends */}

          <div className="level-right">

            {role === 'super' || role === type ?
              <div className="level-item">
                <button className="button is-link"
                  onClick={() => setIsModalVisible(true)}
                >
                  <span className="icon">
                    <i className="fas fa-plus" />
                  </span>
                  <span>Tambah Kegiatan</span>
                </button>
              </div>
              : null
            }

            <div className="level-item">
              <a target="_blank"
                className="button is-primary"
                rel="noopener noreferrer"
                href={printEp}
              >
                <span className="icon">
                  <i className="fas fa-print" />
                </span>
                <span>Cetak</span>
              </a>
            </div>

          </div> {/* level right ends */}
        </div>

        {isLoading ? <Loading /> : null}

        {events.length ?
          <table className="table is-striped is-fullwidth is-hoverable">
            <thead>
              <tr>
                <th>Hari/Tgl</th>
                <th className="is-hidden-mobile">Jam</th>
                {type !== 'adipura' ?
                  <th className="is-hidden-mobile">Tempat</th>
                  : null
                }
                <th>Kegiatan</th>
                {type === 'setda' || type === 'adipura' ?
                  <th className="is-hidden-mobile">Unit Kerja</th>
                  : null
                }
                <th className="is-hidden-mobile">Catatan</th>
              </tr>
            </thead>
            <tbody>
              {events.map(event => {
                const dateTime = event.dateTime ? moment(event.dateTime).local() : null
                const isMatch = match(event.name)
                  || match(event.place)
                  || match(dateTime.format('D'))

                return (
                  <tr key={event._id}
                    style={{ cursor: 'pointer' }}
                    className={!isMatch ? 'is-hidden' : null}
                    onClick={() => {
                      if (role === 'super' || role === type) {
                        setIsModalVisible(true)
                        setSelectedEvent(event)
                      }
                    }}
                  >
                    <td>
                      {dateTime.format('dddd')}
                      <br />
                      {dateTime.format('D MMM')}
                      <br />
                      {dateTime.format('YYYY')}
                      <span className="is-mobile-only">
                        <br />
                        {dateTime.format('H:mm')}
                      </span>
                    </td>

                    <td className="is-hidden-mobile">{dateTime.format('HH:mm')}</td>

                    {type !== 'adipura' ?
                      <td className="is-hidden-mobile">{event.place}</td>
                      : null
                    }

                    <td>
                      <span className="has-text-weight-bold">{event.name}</span>
                      {type !== 'adipura' ?
                        <>
                          {event.place ?
                            <span className="is-mobile-only">
                              <br />
                              <i className="fas fa-map-marker-alt" style={{ marginRight: '.5rem', fontSize: '.75rem', color: '#276cda' }} />
                              {event.place}
                            </span>
                            : null
                          }

                          {username === 'strt' || username === 'kahumum' || username === 'kart' || role === 'setda' ?
                            <>
                              {event.notes ?
                                <span className="is-mobile-only">
                                  <br />
                                  <i className="fas fa-clipboard" style={{ marginRight: '.5rem', fontSize: '.75rem', color: '#276cda' }} />
                                  {event.notes}
                                </span>
                                : null
                              }
                            </>
                            : null
                          }
                        </>
                        : null
                      }
                    </td>

                    {type === 'setda' || type === 'adipura' ?
                      <td className="is-hidden-mobile">
                        {event.pic ? `${event.pic} ` : ''}
                        {event.participant ? `(${event.participant})` : ''}
                      </td>
                      : null
                    }

                    <td className="is-hidden-mobile">{event.notes}</td>
                  </tr>
                )
              })}
            </tbody>
          </table>
          : null
        }

        {isModalVisible ?
          <SetEvent
            type={type}
            event={selectedEvent}
            getEvents={getEvents}
            closeModal={() => {
              setSelectedEvent(null)
              setIsModalVisible(false)
            }}
            notify={props.notify}
            places={places}
          />
          : null
        }

      </div>

    </>
  )
}

function SetEvent(props) {
  const id = props.event ? props.event._id : null
  const type = props.type || 'invalid'
  const admin = getUsername() || 'no-id'

  const { event } = props

  const [name, setName] = useState(event ? event.name : '')
  const [place, setPlace] = useState(event ? event.place : '')
  const [participant, setParticipant] = useState(event ? event.participant : 0)
  const [pic, setPic] = useState(event ? event.pic : '')
  const [notes, setNotes] = useState(event ? event.notes : '')
  const [date, setDate] = useState(event ?
    moment(event.dateTime).format('YYYY-MM-DD')
    :
    moment().format('YYYY-MM-DD')
  )
  const [time, setTime] = useState(event ?
    moment(event.dateTime).format('HH:mm')
    :
    '08:00'
  )

  const [isLoading, setIsLoading] = useState(false)

  function handleSubmit(e) {
    const epx = id ? ep + id : ep
    const method = id ? 'PUT' : 'POST'
    const headers = {
      'Accept': 'application/json',
      'Content-Type': 'application/json',
      'Authorization': 'Bearer ' + getToken(),
    }
    const body = JSON.stringify({
      name: name,
      place: place,
      dateTime: moment(`${date} ${time}`, 'YYYY-MM-DD HH:mm').utc().format('YYYY-MM-DD HH:mm'),
      participant: participant,
      pic: pic,
      type: type,
      admin: admin,
      notes: notes,
    })

    setIsLoading(true)
    fetch(epx, {
      method: method,
      headers: headers,
      body: body,
    }).then(() => {
      props.notify('berhasil menyimpan kegiatan')
      props.getEvents()
      props.closeModal()
    }).catch(err => {
      props.closeModal()
      console.log(err)
    })
    e.preventDefault()
  }

  return (
    <Modal closeModal={props.closeModal}>

      <h1 className="title">
        {id ? 'Ubah ' : 'Tambah '}
        Kegiatan
      </h1>

      <form onSubmit={handleSubmit}>

        <div className="field">
          <label className="label">Kegiatan/Acara</label>
          <div className="control">
            <input
              className="input"
              type="text"
              value={name}
              onChange={e => setName(e.target.value)}
              required
            />
          </div>
        </div>

        {type === 'adipura' ?
          null
          :
          <div className="field">
            <label className="label">Tempat/Lokasi</label>
            <div className="control">
              <input
                className="input"
                type="text"
                value={place}
                onChange={e => setPlace(e.target.value)}
                list="places"
              />
              <datalist id="places">
                {props.places ?
                  props.places.map(place =>
                    <option key={place._id} value={place.name} />
                  )
                  : null
                }
              </datalist>
            </div>
          </div>
        }

        {type === 'setda' || type === 'adipura' ?
          <div className="field">
            <label className="label">Unit Kerja</label>
            <div className="field is-grouped">

              <div className="control">
                <input
                  className="input"
                  type="text"
                  value={pic}
                  onChange={e => setPic(e.target.value)}
                />
              </div>

              {type !== 'adipura' ?
                <div className="control">
                  <input
                    className="input"
                    type="number"
                    value={participant}
                    onChange={e => setParticipant(e.target.value)}
                  />
                </div>
                : null
              }

            </div>
          </div>
          : null
        }

        <div className="field">
          <label className="label">Waktu</label>

          <div className="field is-grouped">
            <div className="control has-icons-left">
              <input
                className="input"
                type="date"
                value={date}
                onChange={e => setDate(e.target.value)}
                required
              />
              <span className="icon is-small is-left">
                <i className="fas fa-calendar" />
              </span>
            </div>

            <div className="control has-icons-left">
              <input
                className="input"
                type="time"
                value={time}
                onChange={e => setTime(e.target.value)}
                required
              />
              <span className="icon is-small is-left">
                <i className="fas fa-clock" />
              </span>
            </div>
          </div>

        </div>

        <div className="field">
          <label className="label">Catatan</label>
          <div className="control">
            <textarea
              className="textarea"
              value={notes}
              onChange={e => setNotes(e.target.value)}
            />
            {/* <input
              className="input"
              type="text"
              value={pic}
              onChange={e => setPic(e.target.value)}
            /> */}
          </div>
        </div>

        <div className="field is-grouped">

          <div className="control">
            <button
              type="submit"
              className={
                `button is-primary
                ${isLoading ? ' is-loading' : ''}
                `
              }
            >Simpan</button>
          </div>

          {id ?
            <div className="control">
              <DeleteEvent id={id}
                getEvents={props.getEvents}
                closeModal={props.closeModal}
                notify={props.notify}
              />
            </div>
            : null
          }

          <div className="control">
            <button
              type="button"
              className="button is-text"
              onClick={props.closeModal}
            >Batal</button>
          </div>

        </div>

      </form>

    </Modal>
  )
}

function DeleteEvent(props) {
  const [isLoading, setIsLoading] = useState(false)
  const [isConfirming, setIsConfirming] = useState(false)

  function handleDelete() {
    if (!isConfirming) return setIsConfirming(true)

    setIsLoading(true)
    fetch(ep + props.id, {
      method: 'DELETE',
      headers: { 'Authorization': 'Bearer ' + getToken() }
    }).then(() => {
      props.getEvents()
      props.notify('Berhasil menghapus kegiatan')
      props.closeModal()
    }).catch(err => {
      console.log(err)
      props.closeModal()
    })
  }

  return (
    <button type="button"
      onClick={handleDelete}
      className={
        `button
          ${!isConfirming ? ' is-warning' : 'is-danger'}
          ${isLoading ? ' is-loading' : ''}
        `
      }
    >
      {!isConfirming ? 'Hapus' : 'Hapus Permanen!'}
    </button>
  )
}

export default Events