import React, { useState, useEffect } from 'react'
import { Loading, Table, Title, Tools, Modal } from './Components'
import { getToken, getRole } from './modules/storageHelper'

const url = 'https://jadwalsetda.herokuapp.com/api/places/'

function Places() {
  const [isLoading, setIsLoading] = useState(false)
  const [isModalVisible, setIsModalVisible] = useState(false)
  const [places, setPlaces] = useState([])
  const [selectedPlace, setSelectedPlace] = useState(null)
  const [searchVal, setSearchVal] = useState('')

  const role = getRole()

  function match(criteria) {
    return criteria
      .toLowerCase()
      .indexOf(searchVal.toLowerCase())
      > -1
  }

  function getPlaces() {
    setIsLoading(true)
    setPlaces([])

    fetch(url)
      .then(res => res.json())
      .then(docs => {
        setIsLoading(false)
        setPlaces(docs)
      })
      .catch(err => console.log(err))
  }

  useEffect(() => getPlaces(), [])

  return (
    <>
      <Title>Daftar Tempat</Title>

      <div className="box">

        {role === 'super' ?
          <Tools
            searchVal={searchVal}
            setSearchVal={setSearchVal}
            setIsModalVisible={setIsModalVisible}
          />
          : null
        }

        {isLoading ? <Loading /> : null}

        {places.length ?
          <Table headers={[
            {text: 'Tempat/Lokasi'},
            {text: 'Maks'},
          ]}>
            {places.map(place => {
              const isMatch = match(place.name)
              return (
                <tr
                  key={place._id}
                  style={{ cursor: 'pointer' }}
                  className={isMatch ? '' : 'is-hidden'}
                  onClick={() => {
                    if (role === 'super') {
                      setIsModalVisible(true)
                      setSelectedPlace(place)
                    }
                  }}
                >
                  <td>{place.name}</td>
                  <td>{place.capacity}</td>
                </tr>
              )
            })}
          </Table>
          : null
        }

        {isModalVisible ?
          <SetPlace
            data={selectedPlace}
            getPlaces={getPlaces}
            closeModal={() => {
              setIsModalVisible(false)
              setSelectedPlace(null)
            }}
          />
          : null
        }

      </div>
    </>
  )
}

function SetPlace(props) {
  const [id, setId] = useState('')
  const [name, setName] = useState('')
  const [capacity, setCapacity] = useState('')
  const [isLoading, setIsLoading] = useState(false)
  const [isDeleting, setIsDeleting] = useState(false)

  useEffect(() => {
    if (props.data) {
      setId(props.data._id)
      setName(props.data.name || '')
      setCapacity(props.data.capacity || '')
    }
  }, [props.data])

  function handleSubmit(e) {
    let formUrl = id ? url + id : url
    let method = id ? 'PUT' : 'POST'
    let headers = {
      'Accept': 'application/json',
      'Content-Type': 'application/json',
      'Authorization': 'Bearer ' + getToken(),
    }
    let body = JSON.stringify({
      name: name,
      capacity: capacity,
    })

    setIsLoading(true)
    fetch(formUrl, {
      method: method,
      headers: headers,
      body: body
    }).then( () => {
      props.getPlaces()
      props.closeModal()
    }).catch(err => console.log(err))

    e.preventDefault()
  }

  function handleDelete() {
    if(!isDeleting) return setIsDeleting(true)
    else setIsLoading(true)

    fetch(url + id, {
      method: 'DELETE',
      headers: {
        'Authorization': 'Bearer ' + getToken(),
      }
    }).then(() => {
      props.getPlaces()
      props.closeModal()
    }).catch(err => console.log(err))
  }

  return (
    <Modal closeModal={props.closeModal}>
      <h1 className="title">
        {id ? 'Ubah ' : 'Tambah '}
        Tempat
      </h1>

      <form onSubmit={handleSubmit}>

        <div className="field">
          <label className="label">Nama</label>
          <div className="control">
            <input
              className="input"
              type="text"
              value={name}
              onChange={e => setName(e.target.value)}
            />
          </div>
        </div>

        <div className="field">
          <label className="label">Kapasitas</label>
          <div className="control">
            <input
              className="input"
              type="number"
              value={capacity}
              onChange={e => setCapacity(e.target.value)}
            />
          </div>
        </div>

        <div className="field is-grouped">
          <div className="control">
            <button
              type="submit"
              className={
                `button is-primary
                ${isLoading & !isDeleting ? ' is-loading' : ''}
                `
              }
            >Simpan</button>
          </div>

          {id ?
            <div className="control">
              <button
                type="button"
                onClick={handleDelete}
                className={
                  `button
                  ${isDeleting ? ' is-danger' : ' is-warning'}
                  ${isDeleting && isLoading ? ' is-loading' : ''}
                  `
                }
              >{!isDeleting ? 'Hapus' : 'Hapus Permanen'}</button>
            </div>
            : null
          }
        </div>

      </form>
    </Modal>
  )
}

export default Places