import React from 'react'

function Notification(props) {
  return (
    <div className="notification is-warning"
      style={{
        position: "fixed",
        bottom: "1rem",
        right: "1.5rem",
        zIndex: "1000",
      }}
    >
      <button type="button"
        className="delete"
        onClick={props.dismiss}
      />
      {props.children}
    </div>
  )
}

export default Notification