import React from 'react'
import moment from 'moment'
import { getIsObserver } from '../modules/storageHelper'

function EventRow(props) {
  const event = props.event
  const isObserver = getIsObserver()

  return (
    <tr>
      <td>
        {event.dateTime ?
          moment(event.dateTime)
            .local()
            .format('HH:mm')
          : null
        }
      </td>

      <td>
        <span className="has-text-weight-bold">
          {event.name}
        </span>

        {event.place ?
          <div className="eventProp">
            <div className="eventPropIcon">
              <i className="fas fa-map-marker-alt" />
            </div>
            {event.place}
          </div>
          : null
        }

        {event.pic ?
          <div className="eventProp">
            <div className="eventPropIcon">
              <i className="fas fa-briefcase" />
            </div>
            {event.pic}
            {event.participant ? ` (${event.participant})` : null}
          </div>
          : null
        }

        {event.notes ?
          isObserver === 'true' ?
            <div className="eventProp">
              <div className="eventPropIcon">
                <i className="fas fa-clipboard" />
              </div>
              {event.notes}
            </div>
            : null
          : null
        }

      </td>
    </tr>
  )
}

export default EventRow