import React, { useState, useEffect } from 'react'
import { Loading, Title, Table, Tools, Modal } from './Components'
import { getToken, getRole } from './modules/storageHelper'

const ep = 'https://jadwalsetda.herokuapp.com/api/users/'

function Users() {
  const [users, setUsers] = useState([])
  const [searchVal, setSearchVal] = useState('')
  const [selectedUser, setSelectedUser] = useState(null)
  const [isLoading, setIsLoading] = useState(false)
  const [isModalVisible, setIsModalVisible] = useState(false)

  const role = getRole()

  function getUsers() {
    setIsLoading(true)
    setUsers([])

    fetch(ep)
      .then(res => res.json())
      .then(docs => {
        setIsLoading(false)
        return setUsers(docs)
      })
      .catch(err => console.log(err))
  }

  function matchUser(c) {
    return c
      .toLowerCase()
      .indexOf(searchVal.toLowerCase())
      > -1
  }

  useEffect(() => getUsers(), [])

  return (
    <>
      <Title>Daftar Pengguna</Title>

      <div className="box">

        {role === 'super' ?
          <Tools
            title='Pengguna'
            searchVal={searchVal}
            setSearchVal={setSearchVal}
            setIsModalVisible={setIsModalVisible}
          />
          : null
        }

        {isLoading ? <Loading /> : null}

        {users.length ?
          <Table headers={[
            {text: 'Nama', isHiddenMobile: true},
            {text: 'Username'},
            {text: 'Role'},
          ]}>
            {users.map(user => {
              const isMatch = matchUser(user.name) || matchUser(user.username)
              const roleLabel = user.role ? user.role.name : null
              const isObserver = user.role ? user.role.isObserver : false
              return (
                <tr
                  key={user._id}
                  style={{ cursor: 'pointer' }}
                  className={!isMatch ? 'is-hidden' : ''}
                  onClick={() => {
                    if(role !== 'super') return
                    setIsModalVisible(true)
                    setSelectedUser(user)
                  }}
                >
                  <td className="is-hidden-mobile">{user.name}</td>
                  <td>{user.username}</td>
                  <td>
                    <div className="tags">
                      {roleLabel ?
                        <div className={`tag
                          ${roleLabel === '-' ? 'is-hidden' : ''}
                          ${roleLabel === 'super' ? ' is-danger' : ''}
                          ${roleLabel === 'verif' ? ' is-primary' : ''}
                          ${roleLabel === 'bupati' ? ' is-link' : ''}
                          ${roleLabel === 'wakil' ? ' is-link' : ''}
                          ${roleLabel === 'sekda' ? ' is-link' : ''}
                          ${roleLabel === 'setda' ? ' is-info' : ''}
                          ${roleLabel === 'adipura' ? ' is-info' : ''}
                        `}>{roleLabel}</div>
                        : null
                      }
                      {isObserver ?
                        <div className="tag is-warning">obs</div>
                        : null
                      }
                    </div>
                  </td>
                </tr>
              )
            })}
          </Table>
          : null
        }

        {isModalVisible ?
          <SetUser
            user={selectedUser}
            getUsers={getUsers}
            closeModal={() => {
              setIsModalVisible(false)
              setSelectedUser(null)
            }}
          />
          : null
        }

      </div>

    </>
  )
}

function SetUser(props) {
  const id = props.user ? props.user._id : null

  let roleInit, obsInit
  if (props.user) {
    if (props.user.role) {
      if (props.user.role.name)
        roleInit = props.user.role.name
      if (props.user.role.isObserver)
        obsInit = props.user.role.isObserver
    }
  }

  const [name, setName] = useState(props.user ? props.user.name : '')
  const [username, setUsername] = useState(props.user ? props.user.username : '')
  const [password, setPassword] = useState('')
  const [role, setRole] = useState(roleInit || '-')
  const [isObserver, setIsObserver] = useState(obsInit || false)
  const [isLoading, setIsLoading] = useState(false)

  function handleSubmit(e) {
    let epx = id ? ep + id : ep
    let method = id ? 'PUT' : 'POST'
    let headers = {
      'Accept': 'application/json',
      'Content-Type': 'application/json',
      'Authorization': 'Bearer ' + getToken(),
    }
    let body = JSON.stringify({
      name: name,
      username: username,
      password: password,
      role: role,
      isObserver: isObserver,
    })

    setIsLoading(true)

    fetch(epx, {
      method: method,
      headers: headers,
      body: body,
    }).then(() => {
      props.getUsers()
      props.closeModal()
    }).catch(err => {
      props.closeModal()
      console.log(err)
    })

    e.preventDefault()
  }

  return (
    <Modal closeModal={props.closeModal}>
      
      <h1 className="title">
        {id ? 'Ubah ' : 'Tambah '}
        Pengguna
      </h1>

      <form onSubmit={handleSubmit}>

        <div className="field">
          <label className="label">Nama Pengguna/Instansi</label>
          <div className="control">
            <input
              className="input"
              type="text"
              value={name}
              onChange={e => setName(e.target.value)}
            />
          </div>
        </div>

        <div className="field">
          <label className="label">Username</label>
          <div className="control">
            <input
              className="input"
              type="text"
              value={username}
              onChange={e => setUsername(e.target.value)}
            />
          </div>
        </div>

        <div className="field">
          <label className="label">Password</label>
          <div className="control">
            <input
              className="input"
              type="password"
              value={password}
              onChange={e => setPassword(e.target.value)}
            />
          </div>
        </div>

        <div className="field">
          <label className="label">Role</label>
          <div className="control">
            <div className="select">
              <select
                value={role}
                onChange={e => setRole(e.target.value)}
              >
                <option value="-">Non Admin</option>
                <option value="super">Super User</option>
                <option value="verif">Verified User</option>
                <option value="bupati">Ajudan Bupati</option>
                <option value="wakil">Ajudan Wakil Bupati</option>
                <option value="sekda">Ajudan Sekretaris Daerah</option>
                <option value="setda">Admin Sekretariat</option>
                <option value="adipura">Admin Gedung Adipura</option>
              </select>
            </div>
          </div>
        </div>

        <div className="field">
          <div className="control">
            <label className="checkbox">
              <input
                type="checkbox"
                checked={isObserver}
                onChange={e => setIsObserver(e.target.checked)}
              />
              &nbsp;Observer?
            </label>
          </div>
        </div>

        <div className="field is-grouped">

          <div className="control">
            <button
              type="submit"
              className={
                `button is-primary
                ${isLoading ? ' is-loading' : ''}
                `
              }
            >Simpan</button>
          </div>

          {id ?
            <DeleteUser
              id={id}
              getUsers={props.getUsers}
              closeModal={props.closeModal}
            />
            : null
          }

        </div>

      </form>

    </Modal>
  )
}

function DeleteUser(props) {
  const [isLoading, setIsLoading] = useState(false)
  const [isConfirming, setIsConfirming] = useState(false)

  function handleDelete() {
    if(!isConfirming) return setIsConfirming(true)

    setIsLoading(true)

    fetch(ep + props.id, {
      method: 'DELETE',
      headers: {
        'Authorization': 'Bearer ' + getToken(),
      }
    }).then(() => {
      props.getUsers()
      props.closeModal()
    }).catch(err => {
      console.log(err)
      props.closeModal()
    })
  }

  return (
    <button
      type="button"
      onClick={handleDelete}
      className={
        `button
          ${!isConfirming ? ' is-warning' : ' is-danger'}
          ${isLoading ? ' is-loading' : ''}
        `
      }
    >
      {!isConfirming ? 'Hapus' : 'Hapus Permanen!'}
    </button>
  )
}

export default Users