import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'

const url = 'https://jadwalsetda.herokuapp.com/api/users/authenticate'

function Login(props) {
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [isLoading, setIsLoading] = useState(false)

  function storeAuthData(doc) {
    localStorage.setItem('token', doc.token)
    localStorage.setItem('username', doc.doc.username)
    localStorage.setItem('name', doc.doc.name)
    localStorage.setItem('role', doc.doc.role.name)
    localStorage.setItem('isObserver', doc.doc.role.isObserver)
  }

  function handleSubmit(e) {
    setIsLoading(true)

    fetch(url, {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        username: username,
        password: password,
      })
    }).then(res => res.json())
      .then(doc => {
        storeAuthData(doc)
        setIsLoading(false)
        props.setName(doc.doc.name || doc.doc.username)
        props.notify('Login berhasil')
        props.history.push('/')
      })
      .catch(err => console.log(err))

    e.preventDefault()
  }

  return (
    <div>
      <section className="hero is-info welcome is-small">
        <div className="hero-body">
          <h1 className="title">Log Masuk</h1>
          <p className="subtitle">Belum punya akun? Hubungi admin</p>
        </div>
      </section>

      <div className="card">
        <div className="card-content">
          <form onSubmit={handleSubmit}>
            <div className="field">
              <label className="label">Username</label>
              <div className="control">
                <input
                  className="input"
                  type="text"
                  value={username}
                  onChange={e => setUsername(e.target.value)}
                />
              </div>
            </div>
            <div className="field">
              <label className="label">Password</label>
              <div className="control">
                <input
                  className="input"
                  type="password"
                  value={password}
                  onChange={e => setPassword(e.target.value)}
                />
              </div>
            </div>
            <div className="field">
              <div className="control">
                <button type="submit"
                  className={`button is-primary
                  ${isLoading ? ' is-loading' : ''}
                  `}
                >Login</button>
              </div>
            </div>
          </form>
        </div>
      </div>

    </div>
  )
}

export default withRouter(Login)
