import React, { useState } from 'react'
import { Link, withRouter } from 'react-router-dom'
import { getRole, getIsObserver } from '../modules/storageHelper'

function Navbar(props) {
  const [isBurgerActive, setIsBurgerActive] = useState(false)

  const role = getRole()
  const isObserver = getIsObserver()

  return (
    <>
      <div className="navbar is-white is-fixed-top">

        <div className="container">
          <div className="navbar-brand">
            <Link to="/" className="navbar-item brand-text">Jadwal Setda</Link>

            <div
              className={`navbar-burger burger${
                isBurgerActive ? ' is-active' : ''
                }`}
              onClick={() => setIsBurgerActive(!isBurgerActive)}
            >
              <span aria-hidden="true" />
              <span aria-hidden="true" />
              <span aria-hidden="true" />
            </div>

          </div>

          <div className={`navbar-menu${
            isBurgerActive ? ' is-active' : ''
            }`}>
            <div className="navbar-start is-mobile-only">

              <div className="navbar-item has-dropdown is-hoverable">
                <Link to="#" className="navbar-link">
                  Gedung &amp; Ruangan
                </Link>
                <div className="navbar-dropdown">

                  <Link to="/events/setda"
                    className="navbar-item"
                    onClick={() => setIsBurgerActive(false)}
                  >Sekretariat</Link>

                  {role === 'super' || role === 'adipura' ?
                    <Link to="/events/adipura"
                      className="navbar-item"
                      onClick={() => setIsBurgerActive(false)}
                    >Gedung Adipura</Link>
                    : null
                  }

                </div>
              </div>

              {role === 'super' || isObserver === 'true' ?
                <div className="navbar-item has-dropdown is-hoverable">
                  <Link to="#" className="navbar-link">Pejabat Daerah</Link>
                  <div className="navbar-dropdown">

                    <Link to="/events/bupati"
                      className="navbar-item"
                      onClick={() => setIsBurgerActive(false)}
                    >Bupati</Link>

                    <Link to="/events/wakil"
                      className="navbar-item"
                      onClick={() => setIsBurgerActive(false)}
                    >Wakil Bupati</Link>

                    <Link to="/events/sekda"
                      className="navbar-item"
                      onClick={() => setIsBurgerActive(false)}
                    >Sekretaris Daerah</Link>

                  </div>
                </div>
                : null
              }
              
              {role === 'super' ?
                <div className="navbar-item has-dropdown is-hoverable">
                  <Link to="#" className="navbar-link">Admin</Link>
                  <div className="navbar-dropdown">

                    <Link to="/places"
                      className="navbar-item"
                      onClick={() => setIsBurgerActive(false)}
                    >Tempat</Link>

                    <Link to="/users"
                      className="navbar-item"
                      onClick={() => setIsBurgerActive(false)}
                    >Pengguna</Link>

                  </div>
                </div>
                : null  
              }

            </div>

            <div className="navbar-end">
              <div className="navbar-item has-dropdown is-hoverable">
                <Link to="#" className="navbar-link">
                  {props.name ? props.name : 'Akun'}
                </Link>

                <div className="navbar-dropdown is-right">

                  <Link to={props.name ? '#' : '/login'}
                    className="navbar-item"
                    onClick={props.name ?
                      () => {
                        // the logout function
                        setIsBurgerActive(false)
                        props.notify('Berhasil log keluar')
                        localStorage.clear()
                        props.setName('')
                        props.history.push('/')
                      }
                      :
                      () => {
                        setIsBurgerActive(false)
                      }
                    }
                  >{!props.name ? 'Log Masuk' : 'Log Keluar'}</Link>

                  <div className="navbar-divider" />

                  <Link to="/about"
                    className="navbar-item"
                    onClick={() => setIsBurgerActive(false)}
                  >Tentang Aplikasi</Link>

                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default withRouter(Navbar)
